<template>
	<div class="SystemInfo">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>系统信息</span></header>
		<div class="container">
			<div>
				<span>版本号:</span>
				<span>{{VersionNum}}</span>
			</div>
			<div>
				<span>登录用户:</span>
				<span>{{LoginUser}}</span>
			</div>
			<div>
				<span>登录仓库:</span>
				<span>{{LoginWarehouse}}</span>
			</div>
			<div>
				<span>组织代码:</span>
				<span>{{OrganizationCode}}</span>
			</div>
			<div>
				<span>服务器地址:</span>
				<textarea  readonly>{{ServerAddress}}</textarea>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SystemInfo",
		data(){
			return{
				VersionNum:'',//版本号
				LoginUser:"",//登录用户
				LoginWarehouse:"",//登录仓库
				OrganizationCode:"",//组织代码
				ServerAddress:"",//服务器地址
			}
		},
		created(){
			var Store= this.$store.state;

			this.VersionNum = Store.App_Version;//版本号
			this.LoginUser = Store.UserID;//登录用户
			this.LoginWarehouse = Store.WarehouseName + "(" + Store.WarehouseCode + ")";//登录仓库
			this.OrganizationCode = Store.Tenant_Code;//组织代码
			this.ServerAddress = Store.Server_Address;//服务器地址
		}
	}
</script>
<style lang="less" scoped>
	.SystemInfo{
		.container{
			div{
				padding: 25/50rem 0;
				border-bottom: 1/50rem solid #e1e1e1;
				overflow: hidden;
				span{
					float: left;

				}
				span:nth-child(1){
					margin-right: 25/50rem;
					width: 170/50rem;
					text-align: right;
				}
				textarea{
					float: left;
					width: 500/50rem;
					height: 200/50rem;
					outline: none;
					padding: 10/50rem;
					box-sizing: border-box;
					font-size: 30/50rem;
				}
			}
		}
	}
	@media screen and (max-width: 329px) and (min-width: 311px){
		.SystemInfo{
			.container{
				div{
					span:nth-child(1){
						width: 200/50rem;
					}
					textarea{
						width: 480/50rem;
						font-size: 36/50rem;
					}
				}
			}
		}
	}
	@media screen and (max-width: 310px){
		.SystemInfo{
			.container{
				div{
					span:nth-child(1){
						margin-right: 10/50rem;
						width: 270/50rem;
					}
					textarea{
						width: 450/50rem;
						height: 250/50rem;
						font-size: 48/50rem;
					}
				}
			}
		}
	}
	@media screen and (max-width: 374px) {
		.SystemInfo{
			.container{
				div{
					border-bottom: 3/50rem solid #e1e1e1;
				}
			}
		}
	}
</style>


